import { TextField } from '@material-ui/core'
import React from 'react'
import '../styles/SignIn.css'

function SignIn() {
    return (
        <div className="signIn">
            <div className="signIn__form">
                <form autoComplete="off">
                    <TextField required id="standard-basic" label="Email" type="email" InputLabelProps={{ className: "textLabel" }} onBeforeInput={{ className: "beforeinput" }} InputProps={{ className: "textcolor" }} /><br /><br />
                    <TextField required id="standard-basic" label="Password" type="password" InputLabelProps={{ className: "textLabel" }} onBeforeInput={{ className: "beforeinput" }} InputProps={{ className: "textcolor" }} />
                    <div className="submit__button" onClick={() => {window.location = "/home";}}>
                        <h4>SIGN IN</h4>
                    </div>
                </form>
                <center>
                    <h6>Forgot Password?</h6>
                    <h6>I don't have an Account,<span onClick={() => {window.location = "/signup";}}>Sign up?</span></h6>

                </center>

            </div>

        </div>
    )
}

export default SignIn

